export default {
  data() {
    return {
      rules: ""
    };
  },
  onLoad() {
    // 推荐规则接口
    this.getRules();
  },
  methods: {
    // 推荐规则接口
    getRules() {
      this.$http.get("/wallet/getRules", {}).then(res => {
        console.log(res);
        this.rules = res.data.rules;
      });
    }
  }
};
